import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, fetchEncryptStream, ObjectKeys, genNumId, parseFloatToDecimal, qryStore, generateComprobanteId } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt, SvgIcon_Cmpt } from "../../../components";


import "./styles.css"
import { CloseModal } from "../../../components/DialogHRM";
import { SelectEfectivoAcc_Cmpt, SelectDropAccount_Cmpt } from "..";
import { SumDocuments } from "../services/server_fetching";
import { DateCalComponent_Cmpt } from "../../Inventory";

const Svg = SvgIcon_Cmpt();

const DateCalComponent =DateCalComponent_Cmpt();

const TextInput = TextInput_Cmpt();

const CheckBox = CheckBox_Cmpt();

const DropInput = SelectEfectivoAcc_Cmpt();

const DropDownCom = SelectDropAccount_Cmpt();



const frmNm = "new_cobro_comprobante"


const CobroManual  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			updStore("PaymentMethodAccount", null);
			updStore("accountPicker", {});
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const save  = async ()  => {
		let ffm = getStore(frmNm);

		let comp2save = [];

		if(!ffm["date"]){
			ffm["date"] = (new Date()).getTime()+800;
		}
		else{
			ffm["date"] =(new Date(ffm["date"])).getTime();
		}


		let comprobanteId = await generateComprobanteId(ffm["date"]);
		ffm["comprobanteId"] = comprobanteId;

		let reference = ""

		let arr = getStore("multi_pay_by_doc") && ObjectKeys(getStore("multi_pay_by_doc"));
		arr && arr?.map((flds, indE)=>{

			let nnfrm = {...ffm};
			let amount = getStore("multi_pay_by_doc")[flds];

			nnfrm["document"] = flds;
			reference += flds +" ; ";
			nnfrm["account"] = getStore("Acc2Pay")?.account;
			nnfrm["subAccount"] = getStore("Acc2Pay")?.subAccount;
			nnfrm["providerId"] = getStore("Acc2Pay")?.providerId;
			nnfrm["type"] = "debit";
			nnfrm["amount"] = amount*1;
			nnfrm["payOrCollect"] = "pay";
			nnfrm["operationId"] = gen16CodeId();

			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm;

			
			comp2save.push(bdyq2)
			
		});

		
		

		if(getStore("ComissionAccount")?.["account"]){
			let nnfrm2 = {...ffm};
			

			nnfrm2["document"] = nnfrm2["collectDocument"];
			nnfrm2["collectDocument"]= reference;
			nnfrm2["account"] = getStore("ComissionAccount")["account"];
			let accountDeb = getStore("account_ids")?.[nnfrm2["account"]];
			if(accountDeb.payOrCollect){
				nnfrm2["payOrCollect"] = accountDeb.payOrCollect;
			}
			nnfrm2["subAccount"] = getStore("ComissionAccount")["subAccount"];
			nnfrm2["type"] = "debit";
			nnfrm2["amount"] = getStore("ComissionAccount")["amount"]*1;
			nnfrm2["operationId"] = gen16CodeId();

			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm2;


			comp2save.push(bdyq2)
		}



		let nnfrm3 = {...ffm};
		let amount = SumCollects(getStore("multi_pay_by_doc"));
		nnfrm3["document"] = nnfrm3["collectDocument"];
		nnfrm3["collectDocument"]= reference;
		nnfrm3["account"] = getStore("PaymentMethodAccount")["account"];
		let accountDeb = getStore("account_ids")?.[nnfrm3["account"]];
		if(accountDeb.payOrCollect){
			nnfrm3["payOrCollect"] = accountDeb.payOrCollect;
		}
		nnfrm3["subAccount"] = getStore("PaymentMethodAccount")["subAccount"];
		nnfrm3["type"] = "credit";
		nnfrm3["amount"] = amount*1;
		if(getStore("ComissionAccount")?.["amount"]){
			nnfrm3["amount"] +=  (getStore("ComissionAccount")["amount"]*1)
		}
		nnfrm3["operationId"] = gen16CodeId();

		let bdyq2 = {...getStore(qryStore())?.add_opera};
		bdyq2.params = {
			businessId:  getStore("profileUser")?.businessId
		}
		bdyq2.form=nnfrm3;


		comp2save.push(bdyq2)


		
		const promises = comp2save.map(async (qry2Sv) => {
			const res = fetchEncryptStream(qry2Sv);
			const td = await res;
			if (td) {
				//console.log(td)
			}
        })      
        await Promise.all(promises);

		
		data?.modalID && CloseModal({id:data?.modalID});


		
		setTimeout(() => {
			updStore("multi_pay_by_doc", null)
			updStore(frmNm, null)
			set_obs(gen16CodeId());
			confirm && confirm();
			
		}, 400);
		
	
	};





	const handlePaymentMethodType = (e) => {
		updStore("ComissionAccount", null);
		updStore("PaymentMethodAccount", null);
		updFrm("paymentType",e);
		set_obs(gen16CodeId())
	};


	const clean = (f) => {
		updStore(frmNm, {});
		set_obs(gen16CodeId())
	};


	const handlePaymentMethodAccount = (acc,subacc) => {
		let nn= {
			"account":acc,
			"subAccount":subacc
		}
		updStore("PaymentMethodAccount", nn);
		set_obs(gen16CodeId())
	}

	const handleComissionAccount = (acc,subacc) => {
		let nn= {
			"account":acc,
			"subAccount":subacc
		}
		updStore("ComissionAccount", nn);
		set_obs(gen16CodeId())
	}

	const updFrmComissionAccount = (f, v, k) => {
		let frm2 = getStore("ComissionAccount") || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore("ComissionAccount", frm2);
		set_obs(gen16CodeId())
	};

	let hasDoc2PorC  = SumDocuments(getStore("toPay_by_doc"),"amount")
	
	

	return (
		<div style={{
			maxHeight: "90vh",
			minHeight:  "88vh",
			width: "560px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Registrando Pago
				</p>
				<div className="flexSpace"/>
				
			</div>

			
			<div className="_dsplFlx ">
				<div className="_dsplFlx " style={{padding: "35px 18px 10px "}}>
					<div className="flexSpace"/>
					<CheckBox 
						data={[
							{label: "Cheque", id:"check"},
							{label: "Tarjeta", id:"card"}
						]}
						active={getStore(frmNm)?.["paymentType"] || "check"}
						updSelect={(e)=>handlePaymentMethodType(e)}
					/>
					<div className="flexSpace"/>
				</div>
				
			</div>
				{getStore(frmNm)?.["paymentType"]==="card"?
					<DropInput 
						query={"Cred"}
						type={getStore(frmNm)?.["paymentType"]}
						handleClick={handlePaymentMethodAccount}
					/>:
					<DropInput 
						query={"Efectivo" }
						type={getStore(frmNm)?.["paymentType"]}
						handleClick={handlePaymentMethodAccount}
					/>
				}

					<div className="_dsplFlx"  >
						<div className="flexSpace"/>
						{getStore("PaymentMethodAccount")?.account	?
							<div className=" acc_dtls">			
								<div className="_dsplFlx"  >
									<div className="code" style={{color: "#646cff"}}>
										{getStore("account_ids")?.[getStore("PaymentMethodAccount")?.account]?.code} 
									</div>
									<div className="name">
									{getStore("account_ids")?.[getStore("PaymentMethodAccount")?.account]?.name}
									</div>
								</div>
								<div className="flexSpace"/>
								{getStore("PaymentMethodAccount")?.subAccount ?
								<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
									<div className="code" >
										{getStore("sub_account_ids")?.[getStore("PaymentMethodAccount")?.subAccount]?.subAccount} 
									</div>
									<div className="name">
									{getStore("sub_account_ids")?.[getStore("PaymentMethodAccount")?.subAccount]?.subAccountName} 
									</div>
								</div>
								:null}
							</div>
							:null
						}
					</div>

					{getStore(frmNm)?.["paymentType"] === "card" && getStore("PaymentMethodAccount")?.account ? 

					<div className={""}>

						<div className={"separator"}/>

						{getStore("ComissionAccount")?.account	?
						<div className=""  >
							<div className=" acc_dtls">			
								<div className="_dsplFlx"  >
									<div className="code" style={{color: "#646cff"}}>
										{getStore("account_ids")?.[getStore("ComissionAccount")?.account]?.code} 
									</div>
									<div className="name">
									{getStore("account_ids")?.[getStore("ComissionAccount")?.account]?.name}
									</div>
								</div>
								<div className="flexSpace"/>
								{getStore("ComissionAccount")?.subAccount ?
								<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
									<div className="code" >
										{getStore("sub_account_ids")?.[getStore("ComissionAccount")?.subAccount]?.subAccount} 
									</div>
									<div className="name">
									{getStore("sub_account_ids")?.[getStore("ComissionAccount")?.subAccount]?.subAccountName} 
									</div>
								</div>
								:null}
								</div>
								<div className="_dsplFlx"  >
									<div className="flexSpace"/>
									<div style={{maxWidth:120}}>
										<TextInput  
											init={getStore("ComissionAccount")?.amount} 
											label={"comision"} 
											width={80} 
											updChanges={(e)=>updFrmComissionAccount("amount",e)}
										/>
									</div>
									<div className="flexSpace"/>
								</div>
							</div>
							:
							<DropDownCom
								placeholder={"buscar cuenta de comision bancaria "} 
								//type={getStore(frmNm)?.["paymentType"]}
								handleClick={handleComissionAccount}
							/>
							}
								<div className={"separator"}/>
						</div>
						
					:null}
				

			<div className="_dsplFlx ">
				<DateCalComponent init={getStore(frmNm)?.["date"]} updDate={(e)=>updFrm("date",e)} />
               
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["collectDocument"]} 
					label={"Documento de Cobro"} 
					width={200} 
					updChanges={(e)=>updFrm("collectDocument",e)}
				/>
			</div>
			<TextInput  
				init={getStore(frmNm)?.["description"]} 
				label={"Descripcion"} 
				width={520} 
				updChanges={(e)=>updFrm("description",e)}
			/>

			

			
			{hasDoc2PorC ? 
			<div className="boxAddAcc" style={{margin: "18px 0"}}>

				<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
					<div className="tr_bar tr_bar_40">
						Documento
					</div>
					<div className="tr_bar tr_bar_20">
						Balance
					</div>
					<div className="tr_bar tr_bar_10">
						
					</div>
					<div className="tr_bar tr_bar_25">
						Por Pagar
					</div>
				</div>
				{hasDoc2PorC && getStore("toPay_by_doc") && ObjectKeys(getStore("toPay_by_doc"))?.map((element, indE)=>
				
					<ListingAccounts 
						key={element} 
						idKey={element} 
						data={getStore("toPay_by_doc")[element]}
						updObs={set_obs}
					/>
				)}
			</div>
			:null}
			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
			
				<div className="card">
					<button onClick={save}>
						Guardar
					</button>
				</div>
			</div>
		

			
		</div>
	);
};

export default CobroManual;


const ListingAccounts  = ({ data, updObs, idKey }) => {
	const [amount, set_amount] = useState(0);



	const handleChange = (v) => {
		set_amount(v);
		updFrm(data?.document,v)
		updObs(gen16CodeId())
	}
	
	
	
	const updFrm = (f, v, k) => {
		let frm2 = getStore("multi_pay_by_doc") || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore("multi_pay_by_doc", frm2);
	};

	
	if(	data?.amount*1=== 0){
		return  null;
	}


	return (
		<div className="_dsplFlx hdr_bar">
			<div className="tr_bar tr_bar_40" style={{marginTop: 10}}>
				{data?.document }
			</div>
			<div className="tr_bar tr_bar_20" style={{marginTop: 10}}>
				${(data?.amount*1).toFixed(2) }
			</div>
			<div className="tr_bar tr_bar_10" style={{marginTop: 10}}>
				{amount > data?.amount ? 
				<div className="icon" >
					<Svg name={"alert"} color={"#c62828"}/>
				</div>
				:null}
			</div>
		 
			<div className="tr_bar tr_bar_25">
				<TextInput  
					init={amount} 
					//label={"Descripcion"} 
					width={80} 
					updChanges={(e)=>handleChange(e)}
				/>
			</div>
		</div>
	)

}



const EstaCuadrado = (obj) => {
	let balance = 0;
	let arr = obj && ObjectKeys(obj);
	arr && arr?.map((element, indE)=>{
		let data = obj[element];
		let ammm =  parseFloatToDecimal(data?.amount,2);
		if(data?.type === "debit"){ 
			balance +=ammm
		}
		else if(data?.type === "credit"){ 
			balance -= ammm
		}
	});
	return arr && arr?.length>0 && balance === 0;
}  




const SumCollects = (obj) => {
	let balance = 0;
	obj && ObjectKeys(obj)?.map((flds, indE)=>{
		balance += parseFloatToDecimal(obj[flds],2);
	});
	return  balance;
}  


