import React from "react";
import ObserveEvent from "../hooks/observeEvent";
import { callDispatchEvent, updStore, getStore, gen16CodeId } from "../stores/Utils";

import './DialogHRM.css'

const obsCode = "listDialog_mdh392"
const DialogHRM = () => {
	const list = getStore("listDialog") || {};
	const observeComponent = ObserveEvent(obsCode);
	let _list = Object.keys(list);
	const close = (Id) => {
		let htm = document.getElementsByTagName("html")[0];
		htm.classList.remove("has-level-two");
		CloseModal({ id: Id });
	};

	return (
		<>
			{_list.map((dg, indg) => {
				let dlg = list[dg];
				if (dlg && dlg.visible) {
					let _Style = {};
					//let _cldg = cleanbase64(dg);
					//_Style[`--s__${_cldg}_zIndex__`]=250;
					//_Style[`--overlay__${_cldg}_zIndex__`]=249;
					if (dlg.height) {
						_Style[`heigth`]=dlg.height;
					}
					if (dlg.width) {
						//_Style[`--s__${_cldg}_width__`]=dlg.width;
					}
					if (dlg.zIndex) {
						//_Style[`--s__${_cldg}_zIndex__`]=dlg.zIndex;
						//_Style[`--overlay__${_cldg}_zIndex__`]=dlg.zIndex-1;
						_Style["zIndex"] = dlg.zIndex - 1;

					}
					let ts = {
						// height: dlg.height,
						// width: dlg.width,
						zIndex: dlg.zIndex,
					};
					let cmp2render = null;
					let data = dlg.data;
					if (dlg.content) {
						cmp2render = React.cloneElement(dlg.content, { data });
					}
					return (
						<div className={"ltr-1kbnjow"} style={_Style} key={dg} >
							<div className={"ltr-hoe9xz"}>
								<div className="ltr-1wao6ny" style={ts} id={dg}>
									{ cmp2render}
								</div>
							</div>
							{dlg.data.overlay ? (
								<div
									className={`DialogHRMOverlay ${
										dlg.visible ? "active" : ""
									}`}
									onClick={() => {
										close(dg)
									}}
								/>
							) : null}
						</div>
					);
				}
			})}
		</>
	);
};

export default DialogHRM;



/*************************************************************************************************************/
/*************************************************************************************************************/
/*************************************************************************************************************/

export const OpenModal = (data) => {
	let list = getStore("listDialog") || {};
	let Id = gen16CodeId();
	if (!list[Id]) {
	  list[Id] = {};
	}
	list[Id]["visible"] = true;
	let _dataProps = {};
	if (!data["props"]) {
	  _dataProps["modalID"] = Id;
	} else {
	  _dataProps = data["props"];
	  _dataProps["modalID"] = Id;
	}
	let doc = document.getElementsByTagName("body")[0];
	if(doc?.style){
		doc.style.overflowY = "hidden"
	}
	list[Id]["isView"] = data["isView"];
	list[Id]["observeResize"] = data["observeResize"];
	if (list[Id]["observeResize"]) {
	  list[Id]["observeInterval"] = setInterval(() => {
		let cmp = document.querySelector(`[dialog-key-id='${Id}']`);
		let dmz = cmp && cmp.getBoundingClientRect();
		if (dmz) {
		  if (
			dmz.width !== list[Id]["width"] ||
			dmz.height !== list[Id]["height"]
		  ) {
			list[Id]["height"] = dmz.height + 10;
			list[Id]["width"] = dmz.width;
			updStore("listDialog", list);
			callDispatchEvent(obsCode);
		  }
		}
	  }, 200);
	}
  
	list[Id]["display"] = true;
	if (data["zIndex"]) {
	  list[Id]["zIndex"] = data["zIndex"];
	}
	if (data["height"]) {
	  list[Id]["height"] = data["height"];
	}
	if (data["width"]) {
	  list[Id]["width"] = data["width"];
	}
	if (data["content"]) {
	  list[Id]["content"] = data["content"];
	}
	list[Id]["data"] = _dataProps;
	updStore("listDialog", list);
	callDispatchEvent(obsCode);
	setTimeout(() => {
		// updComponentStore('listDialog',list);
		let htm = document.getElementsByTagName("html")[0];
		htm.classList.add("has-level-two");
		updStore("modalOpen", Id);
		callDispatchEvent(obsCode);
	}, 125);
  };
  
  export const CloseModal = (data) => {
	let list = getStore("listDialog") || {};
	let Id = data["id"];
	if (list[Id]) {
	  if (list[Id]["observeResize"]) {
		if (list[Id]["observeInterval"]) {
		  clearInterval(list[Id]["observeInterval"]);
		}
	  }
	  list[Id]["display"] = false;
	  list[Id]?.data?.closeEvent?.();
	  updStore("listDialog", list);
	  delete list[Id];
	  let doc = document.getElementsByTagName("body")[0];
	  doc.style.overflowY = null
	  callDispatchEvent(obsCode);
	  setTimeout(() => {
		delete list[Id];
		//updComponentStore('listDialog',list);
		//getDispatch()(setObserveCmpt())
		let htm = document.getElementsByTagName("html")[0];
		htm.classList.remove("has-level-two");
		updStore("modalOpen", null);
	  }, 750);
	}
  };
  