import { fetchEncryptStream, updStore, getStore, gen16CodeId, convertArray2ObjGroupby, ObjectKeys, callbackOnMemory, parseFloatToDecimal, qryStore } from "../../../stores/Utils";





export const getall_collects = async (obj,cb) => {

   

    let bdyq2 = {...getStore(qryStore())?.filter_search_opera};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId,
        ":account": obj?.account,
        ":subAccount": obj?.subAccount
    } 
    
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td && !td.error) {
       
        updStore("collect_list", td);
        updStore("collect_by_doc", GroupbyDoc(td));
        updStore("collect_by_docM", GroupbyDoc(td));
        let idKey = obj?.account + "_"+ obj?.subAccount;

        group_collects_by_provider(td,idKey)
    }
    cb && cb();
};









