export function GroupbyDoc(arr, isPay) {
    var obj = {};

    let arr1 = arr && ObjectKeys(arr)
    if (arr1.length > 0) {
      arr1.map((ele) => {
        let s = arr[ele];
        //let ddt =  (new Date(s["date"])).getTime();
        let keyId =  s["document"]?.trim();
        
      
        if (!obj[keyId]) {
            obj[keyId] = {};
            let ggg = [
                "date",
                "description",
                "document",
                "entryName"
            ]
            ggg.forEach(fld => {
                obj[keyId][fld] = s[fld];
            });
            obj[keyId]["amount"] = 0;
            obj[keyId]["operations"] = [];
        }
        
       
        if(s["type"]){

            let ammm = parseFloatToDecimal(s["amount"],2);
            let bal = parseFloatToDecimal(obj[keyId]["amount"],2);
            let sum = 0;
            if(isPay){
                sum = bal*1 + ammm * (s["type"] === "debit" ?  -1 : 1) ;
            }
            else{
                sum =  bal*1 + ammm *(s["type"] === "credit" ?  -1 : 1 );
            }
            obj[keyId]["amount"] = parseFloatToDecimal(sum,2);
            
            obj[keyId]["operations"].push(convert4acc(s));
        }
      });
    } else {
      obj = null;
    }
    return obj;
  }



 

  export function convert4acc(s) {
    let nItm = {};
    nItm["account"] =  getStore("account_ids")?.[s["account"]];
    nItm["sub_account"] = getStore("sub_account_ids")?.[s["subAccount"]];
    nItm["comprobanteId"] = s["comprobanteId"];
    nItm["date"] = s["date"];
    let ammm = parseFloatToDecimal(s["amount"],2);
    if(s["type"]==="credit"){
        nItm["debit"] = 0;
        nItm["credit"] = ammm;
    }
    else if(s["type"]==="debit"){
        nItm["credit"] = 0;
        nItm["debit"] = ammm;
    }
    return nItm;
  }


  


export const SumDocuments = (obj, key, prvId) =>
callbackOnMemory(
	"SumDocuments",
	[obj,key, prvId],
	() => {
        let balance = 0;
        obj && ObjectKeys(obj)?.map((flds, indE)=>{
            balance += obj[flds]?.[key] * 1
        });
        return  balance;
    }
);


export const SumComprobantes = (obj, key, prvId) =>{
        let balance = 0;
        obj && ObjectKeys(obj)?.map((flds, indE)=>{
            let ammm =  parseFloatToDecimal(obj[flds]["amount"],2);
            balance += ammm * (obj[flds]?.["type"] === "credit"? -1 : 1);
        });
        return  balance;
}



export const group_collects_by_provider = async (td, idKey, cb) => {

    let rec_by_acc = getStore("rec_by_acc") || {};
    rec_by_acc[idKey] = SumComprobantes(td) || "no";
    updStore("rec_by_acc", rec_by_acc);
    cb && cb();
}

